@import url('https://fonts.googleapis.com/css2?family=Handlee&family=Inter:wght@400;700;900&display=swap');

/* ------------------RESET/ОБНУЛЕНИЕ------------------ */

* {
  margin: 0;
  padding: 0;
  border: 0
}

*,
:after,
:before {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box
}

:focus,
:active {
  outline: none
}

a:focus,
a:active {
  outline: none
}

nav,
footer,
header,
aside {
  display: block
}

html,
body {
  scroll-behavior: smooth;
  width: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%
}

input,
button,
textarea {
  font-family: inherit
}

input::-ms-clear {
  display: none
}

button {
  cursor: pointer
}

button::-moz-focus-inner {
  padding: 0;
  border: 0
}

a,
a:visited {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none
}

ul li {
  list-style: none
}

img {
  vertical-align: top
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400
}

/* ------------------COMMON/ОБЩИЕ------------------ */

body {
  font-family: Inter, sans-serif;
  font-weight: 400;
  background-color: #181820;
  overflow: hidden;
}

/* ------------------STYLES/СТИЛИ------------------ */

.App {
  text-align: center;
  height: 100vh;
  width: 80%;
  margin: 0 auto;
  color: #e3e3e3;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
}

.appTitle {

  font-weight: 700;
  letter-spacing: 2px;
  font-size: 50px;
  margin-bottom: 60px;
}